$cblue:#6045EA;
$cabu:#DEDEDE;

.footer{
	background-color: black;
	padding: 70px;
	margin-top:20px;
	color: white;

	footer.muncul{
		opacity: 1;
		transform: translate(0,0);
		transition: 0.5s
	}
	footer{
		opacity: 0;
		transform: translate(0, 100px);
		margin-top: 20px;
		text-align: center;
		color: white;
		a{
			text-decoration: none;
		}
	}

	.footer-container{
		display: flex;
		div{
			box-sizing: border-box;
			margin: 10px;
		}

		.col-footer p:first-child{
			margin-top: 0;
			font-size: 30px;
			color: white;
			font-weight: bold;
		}

		.social-media{
			div{
				display: flex;

				// justify-content: center;
				a.muncul{
					opacity: 1;
					transform: translate(0,0);
					transition: 0.5s;
				}
				a{
					opacity: 0;
					transform: translate(0,-40px);
					margin: 10px;
					background-color:$cblue;
					border-radius:50%;
					font-size: 40px;
					color: unset;
					span{
						i{
							margin: auto;
							text-align: center;
							width: 50px;
							height: 50px;
						}
					} 
				}
			}
		}

		.tentang{
			.ket.muncul{
				opacity: 1;
				transform: translate(0,0);
				transition: 2s;
			}
			.ket{
				opacity: 0;
				transform: translate(0, -40px);
				margin-top: 0;
				text-align: justify;
				font-size: 18px;
				color: $cabu;
			}
		}

		.alamat {
			.alamat-value.muncul{
				opacity: 1;
				transform: translate(0,0);
				transition: 0.5s;
			}
			.alamat-value{
				opacity: 0;
				transform: translate(0, -40px);
				font-size: 18px;
				margin: 0;
				display: flex;
				box-sizing:border-box;

				span{
					margin: auto;
					width: 40px;
				}
				div{
					color :$cabu;
					flex:1;
				}
			}
		}
	}
}

@media screen and (max-width: 800px) {
	.footer{
		padding:15px;
		.footer-container{
			flex-direction: column;
		}
	}
}
@media screen and (max-width: 360px) {
	.footer{
		.footer-container{
			.social-media div{
				flex-wrap: wrap;
				justify-content: space-between;
			}
		}
	}
}